@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300&family=Quattrocento&family=Spartan&display=swap');

.article{
    display: flex;
    margin-top: 35px;
    flex-direction: column;
    max-width: 700px;
    align-self: center;
}

.title {
    margin-top: 10px;
    font-family: 'Cormorant Garamond', serif;
    font-weight: 700;
    padding: 20px;
    font-size: 45px;
    text-align: left;
}

.body {
    font-family: 'Quattrocento', serif;
    text-align: left;
    font-size: 20px;
    font-weight: 400;
    padding: 20px;
    line-height: 40px;
}

@media screen and (max-width: 600px) {
    .article{
        display: flex;
        margin-top: 35px;
        flex-direction: column;
        width: 90%;
        align-self: center;
    }
    
    .title {
        margin-top: 10px;
        font-family: 'Cormorant Garamond', serif;
        font-weight: 700;
        padding: 20px;
        font-size: 45px;
        text-align: left;
        width: 90%;
    }
    
    .body {
        font-family: 'Quattrocento', serif;
        text-align: left;
        font-size: 20px;
        font-weight: 400;
        padding: 20px;
        line-height: 40px;
        width: 90%;
    }
}
